














































































































































































































import { Vue, Component } from 'vue-property-decorator';
import { PortfolioDetailData } from '@/types/views/portfolioDetail.interface';
import BaseImg from '@/components/BaseImg.vue';
import BaseAvatar from '@/components/BaseAvatar.vue';
import { UPDATE_TITLE, UPDATE_BREADCRUMBS } from '@/store/mutation-types';

@Component({
  components: {
    BaseImg,
    BaseAvatar
  },

  // 组件内路由守卫
  beforeRouteEnter(to: any, from: any, next: any) {
    next((vm: any) => {
      // 更新当前页面标题
      vm.$store.commit(UPDATE_TITLE, vm.portfolioDetailInfo.title);
      // 更新当前页面面包屑导航
      vm.$store.commit(UPDATE_BREADCRUMBS, [
        {
          text: '设计',
          // disabled: true,
          href: '/design'
        },
        {
          text: vm.portfolioDetailInfo.title,
          disabled: true,
          href: '/portfolio-detail'
        }
      ]);
      // console.log(vm.portfolioDetailInfo.title);
      // console.log(vm.$route.meta.title);
      // console.log(vm.$route.meta.breadcrumbs);
      // console.log(this.$store.state.title); // Uncaught TypeError: Cannot read property '$store' of undefined
      // console.log(this.$store.state.breadcrumbs); // Uncaught TypeError: Cannot read property '$store' of undefined
    });
  }
})
export default class PortfolioDetail extends Vue {
  portfolioDetailInfo!: object; // 作品详情信息后台数据

  data(): PortfolioDetailData {
    return {
      portfolioDetailInfo: {} // 作品详情信息
    };
  }

  // 路由跳转相关
  goToPage(to: any) {
    if (to.name) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.name === to.name) {
        return;
      }
      this.$router.push({
        name: to.name
      });
    } else if (to.path) {
      // 如果跳转路由和当前路由一致，则不跳转
      if (this.$route.path === to.path) {
        return;
      }
      this.$router.push({
        path: to.path
      });
    }
  }

  // 背景图高度
  get bgHeight() {
    if (this.$vuetify.breakpoint.xs) {
      return 140;
    } else if (this.$vuetify.breakpoint.sm) {
      return 180;
    } else if (this.$vuetify.breakpoint.md) {
      return 240;
    } else if (this.$vuetify.breakpoint.lg) {
      return 360;
    }
    return 400;
  }

  // 头像大小
  get avatarSize() {
    if (this.$vuetify.breakpoint.xs) {
      return 48;
    } else if (this.$vuetify.breakpoint.sm) {
      return 52;
    } else if (this.$vuetify.breakpoint.md) {
      return 56;
    } else if (this.$vuetify.breakpoint.lg) {
      return 60;
    }
    return 64;
  }

  // 获取作品详情信息后台数据
  fetchPortfolioDetailData(id: number) {
    // this.$api.userApi.portfolioDetail.getPortfolioDetail().then(
    //   (response: any) => {
    //     // console.log(response.data);
    //     this.portfolioDetailInfo = response.data;
    //   },
    //   (error: any) => {
    //     console.log(`${error.response.status}: ${error.response.statusText}`);
    //   }
    // );

    this.portfolioDetailInfo = this.$api.userApi.portfolioDetail
      .getPortfolioDetail().data.find((element: any) => {
        return element.id === id;
      });
  }

  // 生命周期钩子函数
  created() {
    this.fetchPortfolioDetailData(Number(this.$route.query.id));
    // 如果没有请求ID的文章信息，则跳转到404页面
    if (!this.portfolioDetailInfo) {
      this.$router.replace({ name: '404' });
    }

    // console.log(this.$store.state.title);
    // console.log(this.$store.state.breadcrumbs);
  }
}
